.sideHeader {
    background-color: #111418;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    left: 0;
    top: 0;
    width: 260px;
    height: 100% !important;
    z-index: 2;
    overflow: hidden;
    overflow-y: auto;
}


.headerTop {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
}


.headerTop h1 {
    color: aliceblue;
}

.headerImg {
    width: 150px;
    border-radius: 50%;
    border: 5px solid gray;
}

.headerNav {
    font-size: 20px;

}


.navUl {
    list-style-type: none;
    margin-left: 0;
    padding-left: 0;
}

.navUl:hover {
    color: darkcyan;

}

.navItem {
    padding: 10px 0 10px 0;
    color: azure;
}

.navItem a,
.navItem a:visited,
.navItem a:hover {
    color: azure;
}


@media screen and (max-width: 990px) {
    .sideHeader {
        display: none;
    }
}

@media screen and (min-width: 990px) {
    .sideHeader2 {
        display: none;
    }
}

.divHeader{
    padding: 7px;
}


.linkHeader {
    color: white;
    margin: 15px;
    font-size: 18px;
}

.linkHeader:hover {
    color: green;
}

